@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
button {
  cursor: pointer;
  padding: 10px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  background-color: #178f3b;
  color: #eee;
  transition: all .2s;
}

button:disabled {
  background-color: #222;
  -webkit-filter: opacity(1) !important;
          filter: opacity(1) !important;
  cursor: not-allowed;
}

button:hover {
  -webkit-filter: opacity(.9);
          filter: opacity(.9);
}
#main-header {
  background-color: #222;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 50px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

#main-header h1 {
  color: #178f3b;
}

#main-header nav {
  border: 2px solid #178f3b;
  border-radius: 7px;
}

@media(max-width: 550px) {
  #main-header {
    flex-direction: column;
  }

  #main-header h1 {
    margin-bottom: 10px;
  }
}
footer {
  background-color: #222;
  padding: 15px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  grid-gap: 15px;
  gap: 15px;
  color: #fff;
}

footer a {
  transition: all .2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

footer a:hover {
  -webkit-filter: opacity(.9);
          filter: opacity(.9);
}
#preview {
  margin: 100px auto 0;
  width: 80vw;
  min-height: 75.4vh;
}

#preview p,
#preview strong,
#preview h4 {
  font-size: 18px;
}

#preview header {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  border: 2px solid #178f3b;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 20px;
}

#preview #general {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-box {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  grid-gap: 20px;
  gap: 20px;
}

.info-box h3 {
  font-size: 20px;
  border-top: 2px solid #178f3b;
  border-bottom: 2px solid #178f3b;
  color: #178f3b;
  padding: 5px 0;
  margin-bottom: 10px;
}

.info-box aside {
  width: 30vw;
}

.info-box aside li {
  list-style: none;
  font-size: 18px;
  text-indent: 20px;
  padding: 2px 0;
}

.info-box section {
  width: 50vw;
}

@media(min-width: 800px) {
  #preview {
    width: 60vw;
  }
}

@media(min-width: 1000px) {
  #preview {
    width: 50vw;
  }
}

@media(max-width: 560px) {
  #preview header {
    flex-direction: column;
  }
}

@media(max-width: 550px) {
  #preview header {
    margin-top: 145px;
  }
}

@media(max-width: 500px) {
  .info-box {
    display: flex;
    flex-direction: column;
  }
}
input {
  padding: 10px;
  font-size: 18px;
  border: 2px solid #178f3b;
  border-radius: 5px;
}
#edit {
  width: 80vw;
  margin: 100px auto 30px;
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 5px 5px 5px #222;
}

#edit section {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  border-bottom: 2px solid #222;
}

#edit section:last-child {
  border: none;
}

#edit section h2 {
  margin-bottom: 10px;
  border-bottom: 2px solid #222;
}

#edit form {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  width: 40vw;
}

#edit form div {
  display: flex;
  justify-content: space-between;
}

#edit form div input {
  width: 19vw;
}

#edit form textarea {
  padding: 10px;
  font-size: 18px;
  border: 2px solid #178f3b;
  border-radius: 5px;
}

#edit section strong,
#edit section p {
  font-size: 18px;
}

#edit span {
  font-weight: bold;
}

#edit section li {
  list-style: none;
  font-size: 18px;
  text-indent: 10px;
}

#edit section .profile {
  width: 40vw;
}

#edit .info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#edit .info p {
  margin: 5px 0;
}

#edit .info div {
  display: flex;
  justify-content: space-around;
}

#edit .edit {
  align-self: center;
  margin-top: 10px;
  width: 100px;
}

#edit button[type=submit] {
  align-self: center;
  margin-top: 10px;
  width: 150px;
}

#general strong {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
}

#expertise strong {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
}

@media (max-width: 550px) {
  #edit {
    margin-top: 150px;
  }
}

@media (max-width: 700px) {
  #edit form {
    width: 50vw;
  }

  #edit form div input {
    width: 24vw;
  }
}

@media (min-width: 800px) {
  #edit {
    width: 60vw;
  }
}

@media (min-width: 1050px) {
  #edit {
    width: 50vw;
  }

  #edit form {
    width: 30vw;
  }

  #edit form div input {
    width: 14vw;
  }

  #edit .info {
    width: 30vw;
  }

  #expertise ul {
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: center;
  } 

  #edit section .profile {
    width: 25vw;
  }
}

@media (min-width: 1500px) {
  #edit .info {
    padding: 0 5vw;
  }
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body, #root, #app {
  background-color: #eee;
}

/* custom scroll */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #c7c7c7; 
  background: #e7e6e6;
}
 
::-webkit-scrollbar-thumb {
  background: #222;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #353545; 
}
